/*
q:not(.quote):before, blockquote:not(.quote):before, .quote:before {
    content: "“";
}

q:not(.quote):after, blockquote:not(.quote):after, .quote:after {
    content: "”";
}*/
blockquote {
  /*border: 0;
  font-size: 1.1em;
  margin: 0 1rem;*/
  position: relative;
}
blockquote:before, blockquote:after {
  content: "" !important;
}
blockquote p:before {
  content: "“";
}
blockquote p:after {
  content: "”";
}
blockquote p:first-child:before,
blockquote p:last-child:after {
  position: relative;
  font-size: 2.25rem;
  line-height: 0.2rem;
  display: inline-block;
  line-height: 0.5rem;
  top: 0.75rem;
}
blockquote p:first-child:before {
  content: "“";
  margin-left: -0.75rem;
  left: -0.25rem;
}
blockquote p:last-child {
  margin: 0;
}
blockquote p:last-child:after {
  content: "”";
  margin-right: -0.75rem;
  right: -0.25rem;
}

