.loading {
  position: relative;
  display: inline-block;
  font-size: 1rem;
  font-weight: normal;
  text-transform: none;
  min-height: 22px;
  min-width: 22px;
  padding-left: 30px;
  line-height: 22px;
}
.loading:before {
  content: "";
  box-sizing: border-box;
  position: absolute;
  top: 0;
  left: 0;
  width: 22px;
  height: 22px;
  /*margin-top: -16px;
  margin-left: -16px;*/
  border-radius: 50%;
  border: 2px solid #D0D0D0;
  border-top-color: #000078;
  animation: spin 0.5s linear infinite;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}
.fade {
  transition: opacity 0.25s ease;
}
.fade:not(.show) {
  opacity: 0;
}

.collapse:not(.show) {
  display: none;
}

.collapsing {
  position: relative;
  height: 0;
  overflow: hidden;
  transition: height 0.35s ease;
}

/*
   Animations for spinners
*/
.syncing {
  transform: trasnslate3d(0, 0, 0);
  -moz-animation: spin-sync 1s infinite ease;
  -o-animation: spin-sync 1s infinite ease;
  -webkit-animation: spin-sync 1s infinite ease;
  animation: spin-sync 1s infinite ease;
  display: inline-block;
  -webkit-transform-origin: 50% 50%;
  -moz-transform-origin: 50% 50%;
  -ms-transform-origin: 50% 50%;
  transform-origin: 50% 50%;
}

@keyframes spin-sync {
  0% {
    -moz-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -moz-transform: rotate(-359deg);
    -o-transform: rotate(-359deg);
    -webkit-transform: rotate(-359deg);
    transform: rotate(-359deg);
  }
}
.spin-clock {
  transform: trasnslate3d(0, 0, 0);
  -moz-animation: spin-clock 60s infinite linear;
  -o-animation: spin-clock 60s infinite linear;
  -webkit-animation: spin-clock 60s infinite linear;
  animation: spin-clock 60s infinite linear;
  display: inline-block;
  -webkit-transform-origin: 50% 50%;
  -moz-transform-origin: 50% 50%;
  -ms-transform-origin: 50% 50%;
  transform-origin: 50% 50%;
}

@keyframes spin-clock {
  0% {
    -moz-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -moz-transform: rotate(359deg);
    -o-transform: rotate(359deg);
    -webkit-transform: rotate(359deg);
    transform: rotate(359deg);
  }
}